.cta {
	background-color: $bcls__orange;
	padding: 0.5rem 1rem;
	border-radius: 0.4rem;
	a {
		color: #fff;
	}
	&:visited {
		color: #fff;
		text-decoration: none;
	}

	&:hover,
	&:focus,
	&:active {
		color: #fff;
	}

	&:focus {
		outline: thin dotted;
	}

	&:hover,
	&:active {
		outline: 0;
	}
}
